<template>
  <nav>
    <v-app-bar class="app-bar-styling-2">
      <v-btn depressed @click="changeRoute()">
        <v-icon>arrow_left</v-icon>
      </v-btn>

      <div style="width:90%;" class="text-center caption">
        <v-label>{{ this.name }}</v-label>
      </div>

      <v-btn
        depressed
        text
        color="white"
        style="padding-right:0;
                margin-right:0px;
                padding-left:5px;"
      >
        <v-icon>shopping_basket</v-icon>
      </v-btn>
    </v-app-bar>
  </nav>
</template>
<script>
export default {
  props: {
    name: {
      required: true,
      type: String,
      default() {
        return "";
      }
    }
  },
  data() {
    return {
      fromRoute: ""
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fromRoute = from;
    });
  },
  methods: {
    changeRoute: function() {
      if (!this.fromRoute.name) {
        this.$router.push("/");
      } else {
        this.$router.back();
      }
    }
  }
};
</script>
<style scoped>
.app-bar-styling-2 {
  z-index: 3 !important;
  padding-top: 10px;
  padding-bottom: 5px;
  min-height: auto;
  background: rgb(255, 141, 112);
  background: linear-gradient(
    135deg,
    rgba(255, 141, 112, 1) 37%,
    rgba(254, 169, 120, 0.9976365546218487) 52%,
    rgba(255, 180, 123, 1) 63%
  );
}
</style>
