<template>
  <vue-pull-refresh :on-refresh="onRefresh">
    <div class="about">
      <Pnav :name="product.name" />

      <v-container v-if="loading" style="height: 400px;">
        <v-row class="fill-height" align-content="center" justify="center">
          <v-col cols="6">
            <v-progress-linear
              color="deep-purple accent-4"
              indeterminate
              rounded
              height="6"
            ></v-progress-linear>
          </v-col>
        </v-row>
      </v-container>

      <div v-else>
        <ProductCarousel :images="product.gallery" />

        <v-card class="detail-radius">
          <v-container>
            <div class="details-container">
              <p v-if="product.discount > 0">
                <span class="red--text">
                  <span style="font-size:12px;">Rs.</span>
                  <span style="font-size:16px;">{{ discountedPrice }}</span>
                </span>
                <strike
                  class="grey--text"
                  style="margin-left:5px;font-size:12px;"
                >
                  {{ product.price }}
                </strike>
              </p>
              <p v-else>Rs. {{ product.price }}</p>

              <hr />

              <h2 class="subtitle-2" style="padding:5px 0;">
                {{ product.name }}
              </h2>
              <hr />
              <br />
              <h3 class="body-2">Description</h3>
              <p class="caption">
                {{ product.description }}
              </p>
            </div>
          </v-container>
        </v-card>
      </div>
    </div>
  </vue-pull-refresh>
</template>
<script>
import Pnav from "@/components/partials/Pnav";
import ProductCarousel from "@/components/ProductCarousel";

import VuePullRefresh from "vue-pull-refresh";

import { setInterval } from "timers";

const fb = require("../firebaseConfig.js");

export default {
  components: {
    Pnav,
    ProductCarousel,
    "vue-pull-refresh": VuePullRefresh
  },
  data() {
    return {
      product: {},
      loading: true,
      fetchingData: false
    };
  },
  computed: {
    discountedPrice() {
      return (
        this.product.price - this.product.price * (this.product.discount / 100)
      );
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData: function() {
      fb.productsCollection
        .doc(this.$route.params.id)
        .get()
        .then(doc => {
          if (doc.exists) {
            this.loading = false;
            this.product = doc.data();
            this.fetchingData = false;
          } else {
            alert("Couldnt get the data!");
          }
        });
    },
    onRefresh: function() {
      let v = this;
      return new Promise(function(resolve, reject) {
        v.fetchingData = true;
        v.fetchData();

        setInterval(function() {
          if (v.fetchingData == false) {
            resolve();
          }
        }, 200);
      });
    }
  }
};
</script>
<style scoped>
.detail-radius {
  margin-top: 10px;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
}
.details-container {
  padding: 10px 30px;
}
</style>
